'use client';
import { useState, useEffect } from 'react';
import Link from 'next/link';
import { Menu, X, Zap } from 'lucide-react';
import { motion, AnimatePresence } from 'framer-motion';

export default function Navbar() {
    const [isOpen, setIsOpen] = useState(false);
    const [scrolled, setScrolled] = useState(false);

    useEffect(() => {
        const handleScroll = () => {
            setScrolled(window.scrollY > 20);
        };
        window.addEventListener('scroll', handleScroll);
        return () => window.removeEventListener('scroll', handleScroll);
    }, []);

    return (
        <nav className={`fixed w-full z-50 transition-all duration-300 ${scrolled ? 'top-4' : 'top-0'}`}>
            <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
                <div className={`mx-auto transition-all duration-500 ${scrolled ? 'bg-black/60 premium-blur border border-white/10 rounded-full px-8' : 'bg-transparent py-4'}`}>
                    <div className="flex justify-between h-16 items-center">
                        <div className="flex-shrink-0 flex items-center gap-3 group cursor-pointer">
                            <div className="bg-blue-600 h-10 w-10 rounded-full flex items-center justify-center group-hover:scale-110 transition-transform shadow-lg shadow-blue-500/20">
                                <Zap className="h-5 w-5 text-white fill-current" />
                            </div>
                            <span className="font-black text-2xl tracking-tighter text-white sharp-text">MEDERSUB</span>
                        </div>

                        <div className="hidden md:flex items-center space-x-10">
                            {['Features', 'Solutions', 'Developers', 'Company'].map((item) => (
                                <Link key={item} href="#" className="text-gray-400 hover:text-white transition-colors text-xs font-black uppercase tracking-[0.2em] sharp-text">
                                    {item}
                                </Link>
                            ))}
                        </div>

                        <div className="hidden md:flex items-center space-x-6">
                            <Link href="/login" className="text-white text-sm font-black uppercase tracking-widest hover:text-red-500 transition-colors sharp-text">Login</Link>
                            <Link href="/register" className="px-8 py-3 bg-red-600 hover:bg-red-500 text-white rounded-full text-xs font-black uppercase tracking-widest transition-all shadow-xl shadow-red-600/30 hover:scale-105 active:scale-95">
                                Open Account
                            </Link>
                        </div>

                        <div className="md:hidden flex items-center">
                            <button onClick={() => setIsOpen(!isOpen)} className="text-gray-300 hover:text-white focus:outline-none p-2 bg-white/5 rounded-lg border border-white/10">
                                {isOpen ? <X className="h-6 w-6" /> : <Menu className="h-6 w-6" />}
                            </button>
                        </div>
                    </div>
                </div>
            </div>

            <AnimatePresence>
                {isOpen && (
                    <motion.div
                        initial={{ opacity: 0, y: -20 }}
                        animate={{ opacity: 1, y: 0 }}
                        exit={{ opacity: 0, y: -20 }}
                        className="md:hidden absolute top-20 left-4 right-4 bg-slate-900/95 backdrop-blur-2xl border border-white/10 rounded-2xl overflow-hidden shadow-2xl z-50 px-4 py-6"
                    >
                        <div className="flex flex-col space-y-4">
                            <Link href="#features" onClick={() => setIsOpen(false)} className="px-4 py-3 rounded-xl text-lg font-medium text-gray-300 hover:text-white hover:bg-white/5 transition-colors">Features</Link>
                            <Link href="#pricing" onClick={() => setIsOpen(false)} className="px-4 py-3 rounded-xl text-lg font-medium text-gray-300 hover:text-white hover:bg-white/5 transition-colors">Pricing</Link>
                            <Link href="/login" onClick={() => setIsOpen(false)} className="px-4 py-3 rounded-xl text-lg font-medium text-gray-300 hover:text-white hover:bg-white/5 transition-colors">Login</Link>
                            <Link href="/register" onClick={() => setIsOpen(false)} className="w-full mt-4 bg-red-600 text-white px-5 py-4 rounded-xl text-lg font-semibold text-center shadow-lg shadow-red-600/20 active:scale-95 transition-transform">
                                Get Started
                            </Link>
                        </div>
                    </motion.div>
                )}
            </AnimatePresence>
        </nav>
    );
}

